<template>
	<view class="body">
		<!-- 搜索 -->
		<view class="search">
			<img src="../../static/volunteer/seacher_icon.png" alt="搜索">
			<input type="text" placeholder-class="defaultInput" placeholder="请输入关键词搜索" v-model="searchText"
				@confirm="search">
		</view>
		<view class="tabbar">
			<u-tabs :list="list1" @click="changeTab" lineWidth="120rpx" lineHeight="6rpx" lineColor="#ffd84d"
				inactiveStyle="font-size:30rpx;" activeStyle="font-size:34rpx;font-weight:600;color:#000;"></u-tabs>
		</view>
		<view class="info">
			<view>{{levelOne}}</view>
			<view v-show="levelOne!='请选择'">{{levelTwo}}</view>
			<view v-show="levelTwo!='请选择'">{{levelThree}}</view>
		</view>
		<view class="list">
			<view class="listLeft">
				<view :class="{cell:true,checked:currentOne===index}" v-for="(item,index) in majorOneList" :key="index" @click="checkOne(item,index)">{{item.name}}</view>
			</view>
			<view class="listRight">
				<view class="cell:true" v-for="(item,index) in majorTwoList" :key="index" @click="checkTwo(item,index)">
					<view :class="{text:true,checked:currentTwo===index}">{{item.name}}</view>
					<view class="childrenList" v-if="currentTwo===index">
						<view :class="{childrenCell:true, checked:currentThree===b}" v-for="(a,b) in majorThreeList" :key="b" @click.stop="checkThree(a,b)">{{a.name}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="sure" @click="backComplete">确定</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { major, nextMajor, } from '@/common/http.api.js';
	export default {
		data() {
			return {
				currentOne:'', //当前选中一级
				currentTwo:'', //当前选中二级
				currentThree:'', //当前选中三级
				searchText:'', //搜索关键词
				levelOne:'请选择', //一级
				levelTwo:'请选择', //二级
				levelThree:'请选择', //三级
				majorOneList:[],
				majorTwoList:[],
				majorThreeList:[],
				major_id:'',
				list1: [{
					name: '学术硕士',
				}, {
					name: '专业硕士',
				}],
			}
		},
		onLoad() {
			major({major_type:'academic_master'}).then(res=>{
				console.log(res)
				this.majorOneList = res.data.data
				nextMajor({id:res.data.data[0].id}).then(res=>{
					console.log(res)
					this.majorTwoList = res.data.data
					nextMajor({id:res.data.data[0].id}).then(res=>{
						console.log(res)
						this.majorThreeList = res.data.data
						
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			//选择一级
			checkOne(item,index) {
				this.levelOne = item.name
				this.currentOne = index
				this.currentTwo = ''
				this.currentThree = ''
				this.levelTwo = '请选择'
				this.levelThree = '请选择'
				nextMajor({id:item.id}).then(res=>{
					console.log(res)
					this.majorTwoList = res.data.data
					nextMajor({id:res.data.data[0].id}).then(res=>{
						console.log(res)
						this.majorThreeList = res.data.data
						
					}).catch(err=>{
						console.log(err)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			//选择二级
			checkTwo(item,index) {
				if(this.levelOne=='请选择'){
					this.$u.toast('请先选择上一级')
				}else{
					this.levelTwo = item.name
					this.currentTwo = index
					this.currentThree = ''
					this.levelThree = '请选择'
					this.major_id = item.id
					nextMajor({id:item.id}).then(res=>{
						console.log(res)
						this.majorThreeList = res.data.data
						if(res.data.data.length<1){
							this.levelThree = ''
						}
					}).catch(err=>{
						console.log(err)
					})
				}
			},
			//选择三级
			checkThree(item,index) {
				if(this.levelTwo=='请选择'){
					this.$u.toast('请先选择上一级')
				}else{
					this.major_id = item.id
					this.levelThree = item.name
					this.currentThree = index
				}
			},
			//搜索
			search() {
				
			},
			changeTab(item) {
				console.log('item', item);
				if(item.index==0){
					major({major_type:'academic_master'}).then(res=>{
						console.log(res)
						this.majorOneList = res.data.data
						nextMajor({id:res.data.data[0].id}).then(res=>{
							console.log(res)
							this.majorTwoList = res.data.data
							nextMajor({id:res.data.data[0].id}).then(res=>{
								console.log(res)
								this.majorThreeList = res.data.data
								
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					major({major_type:'pro_master'}).then(res=>{
						console.log(res)
						this.majorOneList = res.data.data
						nextMajor({id:res.data.data[0].id}).then(res=>{
							console.log(res)
							this.majorTwoList = res.data.data
							nextMajor({id:res.data.data[0].id}).then(res=>{
								console.log(res)
								this.majorThreeList = res.data.data
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}
				this.currentOne = ''
				this.currentTwo = ''
				this.currentThree = ''
				this.levelOne = '请选择'
				this.levelTwo = '请选择'
				this.levelThree = '请选择'
				
			},
			// 回到填写页面
			backComplete() {
				if(this.levelThree=='请选择'){
					this.$u.toast('请先选择')
				}else{
					if(this.levelThree!=''){
						console.log('有第三级')
						uni.navigateBack({
							delta:1
						})
						uni.setStorage({
							key: 'major_id',
							data: this.major_id,
							success: function () {
								console.log('success');
							}
						});
						uni.setStorage({
							key: 'major',
							data: this.levelThree,
							success: function () {
								console.log('success');
							}
						});
					}else{
						console.log('没有第三级')
						uni.navigateBack({
							delta:1
						})
						uni.setStorage({
							key: 'major_id',
							data: this.major_id,
							success: function () {
								console.log('success');
							}
						});
						uni.setStorage({
							key: 'major',
							data: this.levelTwo,
							success: function () {
								console.log('success');
							}
						});
					}
				}
			}
		}
	}
</script>

<style lang="scss">
	.body{
		.search {
			width: 90%;
			margin: 20rpx 5%;
			height: 78rpx;
			border: 1px solid #c7c7c7;
			border-radius: 100rpx;
			display: flex;
			align-items: center;
			background-color: #fff;
		
			img {
				margin-left: 30rpx;
				width: 36rpx;
				height: 36rpx;
			}
		
			input {
				margin-left: 20rpx;
				height: 78rpx;
				width: 80%;
				line-height: 78rpx;
				font-size: 28rpx;
			}
		
			.defaultInput {
				color: #d1d1d1;
				line-height: 78rpx !important;
				font-size: 28rpx;
			}
		}
		.tabbar{
			display: flex;
			justify-content: center;
			padding-bottom: 20rpx;
			.u-tabs__wrapper__nav__item{
				width: 40vw;
			}
		}
		.info{
			background-color: #f7f6fb;
			padding: 0 20rpx;
			display: flex;
			align-items: center;
			view{
				font-size: 28rpx;
				font-weight: 500;
				line-height: 58rpx;
				margin-right: 50rpx;
			}
		}.list{
			background-color: #f7f6fb;
			display: flex;
			font-size: 30rpx;
			text-align: center;
			min-height: 80vh;
			.cell{
				margin-bottom: 1rpx;
				background-color: #fff;
				line-height: 88rpx;
			}
			.checked{
				color: #ee0000;
				background-image: url(../../static/volunteer/checked_icon.png);
				background-position: 90% center;
				background-repeat: no-repeat;
				background-size: 20rpx;
			}
			.listLeft{
				flex: 6;
				margin-right: 4rpx;
			}
			.listRight{
				flex: 6;
				padding-bottom: 100rpx;
				.childrenList{
					.childrenCell{
						background-color: #f7f6fb;
						color: #636364;
						border-bottom: 1rpx solid #dedee1;
					}
					childrenCell:last-child{
						border: none;
					}
					.checked{
						color: #ee0000;
						background-image: url(../../static/volunteer/checked_icon.png);
						background-position: 90% center;
						background-repeat: no-repeat;
						background-size: 20rpx;
					}
				}
			}
		}
		.sure{
			width: 90%;
			background: linear-gradient(to bottom, #ffe592, #ffc343);
			border-radius: 50rpx;
			line-height: 78rpx;
			text-align: center;
			font-size: 28rpx;
			color: #fff;
			position: fixed;
			bottom: 40rpx;
			left: 5%;
			right: 5%;
		}
	}
</style>